
.recent-articles-heading {
  font-size: 1.6rem !important;
}
.recent-article-mb {
  margin-bottom: 3rem;
}
.flex {
  display: flex;
}
.flex-wrap {
  flex-wrap: wrap;
}
.category-card-margin {
  margin: 0 1rem;
}
.text-center {
  margin: 0 auto;
  text-align: center;
}
.category-title {
  font-size: 1.5rem !important;
  font-weight: 500 !important;
  line-height: 1.7;
}
.category-title:after {
  content: none !important;
}
.category-title span {
  display: inline-block;
  font-size: 140%;
  font-weight: 600;
  position: relative;
}
.category-title span:after {
  background-color: var(--primary-4);
  bottom: 14px;
  content: "";
  height: 9px;
  left: 0;
  position: absolute;
  width: 100%;
  z-index: -1;
}
.category-text-color {
  color: var(--black);
}
.nav-word-ed{
  color: #11a3e1;
}
:root {
  --primary-1: #dae7ed;
  --primary-2: #c5dfeb;
  --primary-3: #abd5e7;
  --primary-4: #91c9e1;
  --primary-5: #7fc2de;
  --primary-6: #65b2d3;
  --primary-7: #50a9d0;
  --primary-8: #2c89b1;
  --primary-9: #267597;
  --primary-10: #215d76;
  --grey-10: #2e2e2e;
  --grey-8: #404040;
  --grey-7: #595959;
  --grey-6: #545454;
  --grey-5: #707070;
  --grey-3: #d9d9d9;
  --grey-2: #f0f0f0;
  --grey-1: #f5f5f5;
  --green-dark: #1d9527;
  --green-light: #d4edda;
  --red-dark: #bb2525;
  --red-light: #e66b6b;
  --white: #fafafa;
  --black: #131313;
  --ff-primary: "Albert Sans", sans-serif;
  --ff-secondary: "Montserrat", sans-serif;
  --spacing: 1.5px;
  --radius: 0.25rem;
  --lightest-shadow: 0 5px 25px rgba(0, 0, 0, 0.08);
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 700px;
  --page-bg: #fbfcff;
  --border-color-light: #e1e1e1;
  --card-icon-bg: #b6eaff;
}
*,
:after,
:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background: var(--page-bg);
  color: var(--grey-10);
  font-family: var(--ff-primary), "Helvetica", sans-serif;
  font-size: 0.875rem;
  letter-spacing: 0.5px;
  line-height: 1.5;
}
ul {
  list-style-type: none;
}
ol,
ul {
  font-weight: 400;
}
a {
  color: var(--primary-8);
  text-decoration: none;
  transition: color 0.1s ease-in-out;
}
a:hover {
  color: var(--primary-9);
}
a.not-blue {
  color: var(--grey-9);
  text-decoration: none;
  transition: color 0.1s ease-in-out;
}
a.not-blue:hover {
  opacity: 0.95;
}
h1,
h2,
h3,
h4 {
  color: var(--grey-8);
  font-family: var(--ff-secondary);
  letter-spacing: var(--spacing);
  line-height: 1.25;
  margin-bottom: 0.75rem;
  text-transform: capitalize;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  color: var(--grey-7);
  font-size: 17px;
  font-weight: 400;
  margin-bottom: 1.25rem;
}
strong {
  font-weight: 600 !important;
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 3.2rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 17px;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
.btn {
  border: 1px solid;
  border-radius: 50px;
  color: var(--primary-8);
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: var(--spacing);
  padding: 0.575rem 1.2rem;
  text-transform: uppercase;
  -webkit-transition: var(--transition);
  transition: var(--transition);
}
.btn,
.btn:hover {
  background: var(--primary-8);
}
.btn:hover {
  color: var(--white);
}
.btn-outline-primary {
  background: 0 0;
  border-color: var(--primary-8);
  color: var(--primary-8);
}
.page-center {
  margin: 0 auto;
  max-width: var(--max-width);
  width: 90vw;
}
@media screen and (min-width: 992px) {
  .page-center {
    width: 95vw;
  }
}
.sidebar {
  align-items: center;
  background: var(--grey-1);
  display: grid;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  transform: translateX(-100%);
  transition: var(--transition);
  visibility: hidden;
  width: 100%;
  z-index: 999;
}
.showSidebar {
  transform: translateX(0) !important;
  visibility: visible;
}
.sidebar-links li {
  margin-bottom: 1.5rem;
}
.sidebar-links .page-link {
  color: var(--grey-10);
  display: block;
  font-size: 2rem;
  font-weight: 700;
}
.sidebar .categories {
  flex-direction: column;
  margin: 1rem 0;
}
.sidebar .category {
  color: var(--grey-5);
  cursor: pointer;
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  padding-left: 1rem;
  text-transform: capitalize;
}
@media screen and (min-width: 800px) {
  .sidebar {
    transform: translateX(-100%);
  }
}
.close-btn {
  background: transparent;
  border: transparent;
  cursor: pointer;
  font-size: 3rem;
  position: absolute;
  right: 1rem;
  top: 1rem;
}
.posts {
  margin: 0 auto 4rem;
  max-width: var(--max-width);
  width: 85vw;
}
@media screen and (min-width: 992px) {
  .posts {
    width: 92vw;
  }
}
.error-page {
  background: var(--grey-1);
  background: var(--primary-10);
  display: grid;
  min-height: calc(100vh - 7rem);
  place-items: center;
  text-align: center;
}
.error-page span {
  color: var(--black);
  font-size: 10rem;
}
.error-page h3 {
  margin-bottom: 2rem;
  padding: 0 2rem;
}
@media screen and (min-width: 992px) {
  input[type="text"] {
    border-bottom-left-radius: var(--radius);
    border-top-left-radius: var(--radius);
  }
  input[type="email"] {
    border-right: none;
  }
}
.code {
  background: #1e1e1e;
  border-radius: var(--radius);
  color: var(--primary-5);
  font-size: 1.2rem;
  margin: 2rem 0;
  overflow-x: scroll;
  padding: 1rem 1.5rem;
  position: relative;
}
.code pre {
  font-family: Courier New, Courier, monospace;
}

