.section-title {
  font-size: 20px;
  font-weight: 800;
  letter-spacing: 0.2rem;
  margin-bottom: 35px;
  position: relative;
  text-transform: uppercase;
}
.section-title:after {
  background-color: var(--primary-7);
  bottom: -10px;
  content: "";
