.card {
  border-radius: 5px;
  box-shadow: 0 15px 25px rgba(30, 30, 30, 0.1);
  overflow: hidden;
}
.card-body {
  padding: 30px 25px;
}
.card-body h3 {
  font-size: 17px;
  margin-bottom: 1rem;
}
.trending-card {
  align-items: center;
  display: flex;
  margin-bottom: 1.4rem;
}
.trending-img {
  height: 85px;
  object-fit: cover;
  width: 85px;
}
.trending-meta {
  margin-left: 1rem;
}
.card-meta {
  align-items: center;
  color: #000;
  display: flex;
  flex-wrap: wrap;
  font-size: 12px;
  margin-bottom: 0;
}
.card-meta li:not(:last-child) {
  border-right: 1px solid #ebebeb;
  padding-right: 8px;
}
.list-inline {
  list-style: none;
  padding-left: 0;
}
.list-inline-item {
  margin-bottom: 0.8rem;
}
.list-inline-item:not(:last-child) {
  margin-right: 0.5rem;
}
.category-link {
  border-radius: 30px;
  box-shadow: 0 3px 25px currentColor;
  color: #313131;
  display: inline-block;
  font-size: 100%;
  padding: 0.2rem 0.6rem;
  transition: all 0.1s ease;
}
.category-link:hover {
  filter: brightness(108%);
}
.card-meta-tag {
  margin-bottom: 0;
}
.card-meta-tag li {
  border: 0 !important;
  margin-bottom: 0 !important;
  padding-right: 0 !important;
}
.excerpt {
  padding: 5px 0;
